"use client";



import { useState, useEffect } from "react";

import { useSearchParams, useRouter } from "next/navigation";

import OpportunityCard from "@/components/OpportunityCard";

import Navbar from "@/components/Navbar";

import Footer from "@/components/Footer";

import SEO from "@/components/SEO";

import { Loader2, Sparkles, Filter } from "lucide-react";

import { opportunitiesService } from "@/lib/firestore";

import type { Opportunity } from "@/types/admin";
import { OPPORTUNITY_TYPES } from "@/types/admin";

import { useStudentAuth } from "@/lib/studentAuth";

import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue
} from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { X, Calendar as CalendarIcon, Tag as TagIcon, Check } from "lucide-react";
import { cn } from "@/lib/utils";

import { motion, AnimatePresence } from "framer-motion";



const Explore = () => {

    const [loading, setLoading] = useState(true);

    const [opportunities, setOpportunities] = useState<Opportunity[]>([]);

    const [categoryCounts, setCategoryCounts] = useState<Record<string, number>>({});
    const [allTags, setAllTags] = useState<string[]>([]);

    // Advanced Filter State
    const [statusFilter, setStatusFilter] = useState<'all' | 'active' | 'closed'>('all');
    const [monthFilter, setMonthFilter] = useState<string>('all');
    const [yearFilter, setYearFilter] = useState<string>('all');
    const [selectedTags, setSelectedTags] = useState<string[]>([]);
    const [searchQuery, setSearchQuery] = useState('');

    const searchParams = useSearchParams();

    const router = useRouter();

    const { student } = useStudentAuth();

    const [mounted, setMounted] = useState(false);



    useEffect(() => {

        setMounted(true);

    }, []);



    const categoryFilter = searchParams?.get("category");

    const isForYou = searchParams?.get("filter") === "for-you";



    useEffect(() => {

        const fetchOpps = async () => {

            try {

                setLoading(true);

                // Fetch all published opportunities
                const data = await opportunitiesService.getAll({ status: "published" });

                // Extract all unique tags
                const tags = new Set<string>();
                data.forEach(opp => {
                    opp.tags?.forEach(tag => tags.add(tag));
                });
                setAllTags(Array.from(tags).sort());

                // Calculate counts BEFORE applying filters
                const counts: Record<string, number> = {};
                data.forEach(opp => {
                    counts[opp.type] = (counts[opp.type] || 0) + 1;
                });
                setCategoryCounts(counts);

                // Apply Category Filter (from URL)
                let filteredData = [...data];
                if (categoryFilter) {
                    filteredData = filteredData.filter(opp =>
                        opp.type === categoryFilter ||
                        opp.tags?.includes(categoryFilter)
                    );
                }

                // Apply Status Filter
                const now = Date.now();
                if (statusFilter !== 'all') {
                    filteredData = filteredData.filter(opp => {
                        const isClosed = opp.dates?.registrationEnd ? new Date(opp.dates.registrationEnd).getTime() < now : false;
                        return statusFilter === 'active' ? !isClosed : isClosed;
                    });
                }

                // Apply Month & Year Filter
                if (monthFilter !== 'all' || yearFilter !== 'all') {
                    filteredData = filteredData.filter(opp => {
                        if (!opp.dates?.registrationEnd) return false;
                        const date = new Date(opp.dates.registrationEnd);
                        const matchMonth = monthFilter === 'all' || (date.getMonth() + 1).toString() === monthFilter;
                        const matchYear = yearFilter === 'all' || date.getFullYear().toString() === yearFilter;
                        return matchMonth && matchYear;
                    });
                }

                // Apply Tags Filter
                if (selectedTags.length > 0) {
                    filteredData = filteredData.filter(opp =>
                        selectedTags.every(tag => opp.tags?.includes(tag))
                    );
                }

                // Apply Search Filter
                if (searchQuery) {
                    const query = searchQuery.toLowerCase();
                    filteredData = filteredData.filter(opp =>
                        opp.title.toLowerCase().includes(query) ||
                        opp.organizer?.toLowerCase().includes(query) ||
                        opp.shortDescription?.toLowerCase().includes(query)
                    );
                }

                if (isForYou && student) {
                    filteredData = filteredData.sort((a, b) => {
                        const aMatch = a.tags?.some(tag => student.interests?.includes(tag)) || a.type.includes(student.interests?.[0]);
                        const bMatch = b.tags?.some(tag => student.interests?.includes(tag)) || b.type.includes(student.interests?.[0]);
                        return (aMatch === bMatch) ? 0 : aMatch ? -1 : 1;
                    });
                }

                setOpportunities(filteredData);
            } catch (error) {
                console.error("Error fetching opportunities:", error);
            } finally {
                setLoading(false);
            }
        };
        fetchOpps();
    }, [categoryFilter, isForYou, student, statusFilter, monthFilter, yearFilter, selectedTags, searchQuery]);

    const months = [
        { value: '1', label: 'January' },
        { value: '2', label: 'February' },
        { value: '3', label: 'March' },
        { value: '4', label: 'April' },
        { value: '5', label: 'May' },
        { value: '6', label: 'June' },
        { value: '7', label: 'July' },
        { value: '8', label: 'August' },
        { value: '9', label: 'September' },
        { value: '10', label: 'October' },
        { value: '11', label: 'November' },
        { value: '12', label: 'December' },
    ];

    const currentYear = new Date().getFullYear();
    const years = Array.from({ length: 5 }, (_, i) => (currentYear + i).toString());

    const toggleTag = (tag: string) => {
        setSelectedTags(prev =>
            prev.includes(tag) ? prev.filter(t => t !== tag) : [...prev, tag]
        );
    };



    return (

        <div className="min-h-screen bg-background text-foreground antialiased selection:bg-primary/20">

            <SEO

                title="Explore Opportunities | Myark"

                description="Browse all scholarships, competitions, and events. Your quest for success starts here."

                url="https://myark.in/explore"

            />

            <Navbar />



            <main className="pt-24 pb-20 px-4 min-h-screen">

                <div className="max-w-6xl mx-auto">

                    {/* Header */}

                    <div className="flex flex-col md:flex-row md:items-end md:justify-between gap-6 mb-12">

                        <div>

                            <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-primary/10 text-primary text-xs font-black uppercase tracking-widest mb-4" suppressHydrationWarning>

                                <Sparkles className="w-3 h-3" />

                                {opportunities.filter(o => {
                                    const isClosed = o.dates?.registrationEnd ? new Date(o.dates.registrationEnd).getTime() < Date.now() : false;
                                    return !isClosed;
                                }).length} Active

                                <span className="opacity-30 mx-1">|</span>

                                {opportunities.filter(o => {
                                    const isClosed = o.dates?.registrationEnd ? new Date(o.dates.registrationEnd).getTime() < Date.now() : false;
                                    return isClosed;
                                }).length} Closed

                            </div>

                            <h1 className="font-display text-4xl md:text-5xl font-bold">

                                {categoryFilter ? `${categoryFilter} Qusts` : "Explore Everythingüî•"}

                            </h1>

                        </div>



                        {/* Filters Section */}
                        <div className="space-y-6 mb-12">
                            {/* Category Toggles */}
                            <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide no-scrollbar -mx-4 px-4 md:mx-0 md:px-0">
                                <Button
                                    variant={!categoryFilter ? "default" : "outline"}
                                    size="sm"
                                    onClick={() => router.push('/explore')}
                                    className="whitespace-nowrap rounded-full px-5"
                                >
                                    All Quest Types
                                </Button>
                                {OPPORTUNITY_TYPES.filter(type => (categoryCounts[type.id] || 0) > 0).map((type) => (
                                    <Button
                                        key={type.id}
                                        variant={categoryFilter === type.id ? "default" : "outline"}
                                        size="sm"
                                        onClick={() => router.push(`/explore?category=${type.id}`)}
                                        className="whitespace-nowrap rounded-full px-5"
                                    >
                                        {type.name}s ({categoryCounts[type.id]})
                                    </Button>
                                ))}
                            </div>

                            {/* Advanced Filters Grid */}
                            <div className="grid grid-cols-1 md:grid-cols-4 gap-4 bg-muted/30 p-4 rounded-3xl border border-white/5">
                                {/* Status Filter */}
                                <div className="space-y-2">
                                    <label className="text-[10px] font-black uppercase tracking-widest text-muted-foreground ml-1">Status</label>
                                    <Select value={statusFilter} onValueChange={(v) => setStatusFilter(v as 'all' | 'active' | 'closed')}>
                                        <SelectTrigger className="bg-background/50 border-white/10 rounded-xl h-11">
                                            <SelectValue placeholder="All Status" />
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="all">All Missions</SelectItem>
                                            <SelectItem value="active">Active Now üî•</SelectItem>
                                            <SelectItem value="closed">Past Deadlines üí®</SelectItem>
                                        </SelectContent>
                                    </Select>
                                </div>

                                {/* Month Filter */}
                                <div className="space-y-2">
                                    <label className="text-[10px] font-black uppercase tracking-widest text-muted-foreground ml-1">Deadline Month</label>
                                    <Select value={monthFilter} onValueChange={setMonthFilter}>
                                        <SelectTrigger className="bg-background/50 border-white/10 rounded-xl h-11">
                                            <div className="flex items-center gap-2">
                                                <CalendarIcon className="w-3.5 h-3.5 text-muted-foreground" />
                                                <SelectValue placeholder="Select Month" />
                                            </div>
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="all">Any Month</SelectItem>
                                            {months.map(m => (
                                                <SelectItem key={m.value} value={m.value}>{m.label}</SelectItem>
                                            ))}
                                        </SelectContent>
                                    </Select>
                                </div>

                                {/* Year Filter */}
                                <div className="space-y-2">
                                    <label className="text-[10px] font-black uppercase tracking-widest text-muted-foreground ml-1">Deadline Year</label>
                                    <Select value={yearFilter} onValueChange={setYearFilter}>
                                        <SelectTrigger className="bg-background/50 border-white/10 rounded-xl h-11">
                                            <SelectValue placeholder="Select Year" />
                                        </SelectTrigger>
                                        <SelectContent>
                                            <SelectItem value="all">Any Year</SelectItem>
                                            {years.map(y => (
                                                <SelectItem key={y} value={y}>{y}</SelectItem>
                                            ))}
                                        </SelectContent>
                                    </Select>
                                </div>

                                {/* Search */}
                                <div className="space-y-2">
                                    <label className="text-[10px] font-black uppercase tracking-widest text-muted-foreground ml-1">Search Keywords</label>
                                    <div className="relative">
                                        <Input
                                            placeholder="Search missions..."
                                            value={searchQuery}
                                            onChange={(e) => setSearchQuery(e.target.value)}
                                            className="bg-background/50 border-white/10 rounded-xl h-11 pl-4 pr-10"
                                        />
                                        {searchQuery && (
                                            <button
                                                onClick={() => setSearchQuery('')}
                                                className="absolute right-3 top-1/2 -translate-y-1/2 text-muted-foreground hover:text-white"
                                            >
                                                <X className="w-4 h-4" />
                                            </button>
                                        )}
                                    </div>
                                </div>
                            </div>

                            {/* Tags Selection */}
                            {allTags.length > 0 && (
                                <div className="space-y-3">
                                    <div className="flex items-center justify-between">
                                        <div className="flex items-center gap-2 text-xs font-bold text-muted-foreground uppercase tracking-widest">
                                            <TagIcon className="w-3.5 h-3.5" />
                                            <span>Filter by Interests</span>
                                        </div>
                                        {selectedTags.length > 0 && (
                                            <button
                                                onClick={() => setSelectedTags([])}
                                                className="text-[10px] font-black text-primary uppercase hover:underline"
                                            >
                                                Clear All ({selectedTags.length})
                                            </button>
                                        )}
                                    </div>
                                    <div className="flex flex-wrap gap-2">
                                        {allTags.map(tag => (
                                            <Badge
                                                key={tag}
                                                variant={selectedTags.includes(tag) ? "default" : "outline"}
                                                className={cn(
                                                    "cursor-pointer px-4 py-1.5 rounded-full transition-all text-xs font-bold hover:scale-105 active:scale-95",
                                                    selectedTags.includes(tag)
                                                        ? "bg-primary text-primary-foreground border-transparent"
                                                        : "bg-white/5 border-white/10 hover:border-white/30"
                                                )}
                                                onClick={() => toggleTag(tag)}
                                            >
                                                {selectedTags.includes(tag) && <Check className="w-3 h-3 mr-2" />}
                                                {tag}
                                            </Badge>
                                        ))}
                                    </div>
                                </div>
                            )}
                        </div>

                    </div>



                    {/* Grid */}

                    {loading ? (

                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 opacity-50">

                            {[1, 2, 3, 4, 5, 6].map(i => (

                                <div key={i} className="h-96 rounded-[40px] bg-muted animate-pulse" />

                            ))}

                        </div>

                    ) : opportunities.length > 0 ? (

                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">

                            {opportunities.map((opp, index) => (

                                <OpportunityCard

                                    key={opp.id}

                                    id={opp.id}

                                    title={opp.title}

                                    organization={opp.organizer || "Admin"}

                                    type={opp.type as "competition" | "scholarship" | "olympiad" | "workshop"}

                                    deadline={(() => {
                                        if (opp.dates?.registrationEndDescription) return opp.dates.registrationEndDescription;
                                        if (!mounted || !opp.dates?.registrationEnd) return "Ending Soon";
                                        const end = new Date(opp.dates.registrationEnd).getTime();
                                        const diff = Math.ceil((end - Date.now()) / (1000 * 60 * 60 * 24));
                                        return diff > 0 ? `${diff} days left` : "Ending Soon";
                                    })()}

                                    participants={opp.applicationCount || 0}

                                    prize={opp.prizes?.first || "Certificate"}

                                    isClosed={opp.dates?.registrationEnd ? new Date(opp.dates.registrationEnd).getTime() < Date.now() : false}

                                    delay={index * 50}

                                />

                            ))}

                        </div>

                    ) : (

                        <div className="text-center py-32 glass-card rounded-[40px] border-dashed">

                            <p className="text-xl text-muted-foreground font-medium">No quests found here right now. Check back soon! ÔøΩx"ÔøΩÔ∏è‚¨çÔøΩ"Ô∏è</p>

                        </div>

                    )}

                </div>

            </main>

            <Footer />

        </div>

    );

};



export default Explore;

